}

.button-call {
  width: 55px;
  height: 55px;
  z-index: 99;
  cursor: pointer;
  background-color: #b71c1c;
  border: none;
  border-radius: 50%;
  outline: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
}

.button-call > svg {
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.button-call:before {
  width: 100%;
  height: 100%;
  content: "";
  z-index: 3;
  opacity: .5;
  border: 1px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

.button-call > svg {
  margin: 4px 0 0 1px;
  transition: all .3s;
}

.button-call:hover:before {
  opacity: 1;
  transition: all .6s;
  transform: translateY(-5px)translateX(-5px);
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 100;
  transition: all .4s ease-out;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__content {
  width: 150px;
  text-align: center;
  background-color: #000;
  border: 2px solid #fff;
  border-top: 0;
  border-radius: 0 0 5px 5px;
  margin-top: 50px;
  padding: 20px;
  transition: all .4s ease-out;
  position: absolute;
  right: 0;
}

.modal--open {
  display: block;
}

.modal__list > li > a {
  color: #fff;
  text-transform: uppercase;
  font: 300 13px / 3rem Oswald, Helvetica;
}

.modal__list > li > a:hover {
  color: #b71c1c;
}

.modal__icons {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.modal__icons--item:hover {
  fill: #b71c1c;
}

