#scroll-to-top {
  z-index: 99;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 10px;
  outline: none;
  display: none;
  position: fixed;
  bottom: 100px;
  left: 20px;
}

#scroll-to-top > svg {
  animation: scrollUp 1s ease-in-out infinite;
}

#scroll-to-top:before {
  width: 100%;
  height: 100%;
  content: "";
  z-index: 3;
  opacity: .5;
  border: 1px solid #fff;
  border-radius: 10px;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes scrollUp {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-15px);
  }

  100% {
    transform: translateY(0);
  }
}

#scroll-to-top > svg {
  margin: 4px 0 0 1px;
  transition: all .3s;
}

#scroll-to-top:hover:before {
  opacity: 1;
  transition: all .6s;
  transform: translateY(-5px)translateX(-5px);
}

